<template>
    <div class="parts-mainten">
        <Headers></Headers>
        <div class="separator">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :key="item" v-for="item in breadList">{{ item }}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="basic-info block" v-show="!editOrNew.showOrHide">
            <el-form :inline="true" :model="form" label-width="85px" class="parts-form-inline">
                <div class="item-row">
                    <el-form-item label="">
                        <el-input v-model="form.brand" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.number" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.tempNum" placeholder=""></el-input>
                    </el-form-item>
                </div>
                <div class="item-row">
                    <el-form-item label="">
                        <el-input v-model="form.partName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.partsNikName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.suitType" placeholder=""></el-input>
                    </el-form-item>
                </div>
            </el-form>
            <div class="button-list">
                <div class="button-box">
                    <el-button type="primary"></el-button>
                    <el-button type="primary" class="reset" @click="reset"></el-button>
                </div>
            </div>
            <div class="parts-hr"></div>
            <div class="new-button">
                <el-button type="primary" @click="newParts"></el-button>
            </div>
            <div class="parts-form">
                <el-table :data="table.list" height="250" type=index border style="width: 100%">
                    <el-table-column prop="brand" label="" align="center" width="80"></el-table-column>
                    <el-table-column prop="num" label="" align="center" width="200"></el-table-column>
                    <el-table-column prop="tempNum" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="partsName" label="" align="center" width="235"></el-table-column>
                    <el-table-column prop="partsNikeName" label="" align="center" width="235"></el-table-column>
                    <el-table-column prop="purchasePrice" label="" align="center" width="80"></el-table-column>
                    <el-table-column prop="_4sPrice" label="4S" align="center" width="80"></el-table-column>
                    <el-table-column prop="basicSalePrice" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="suitType" label="" align="center" width="80">
                        <template slot-scope="scope">
                            <div v-if="scope.row.suitType">
                                <el-button type="text" class="solt-button" @click="showPartsSuitType($event)"
                                        size="small">
                                </el-button>
                            </div>
                            <div v-else></div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="oeOem" label="OE-OEM" align="center" width="90">
                        <template slot-scope="scope">
                            <el-button type="text" class="solt-button" @click="showPartsDialog($event)" size="small">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="operate" label="" width="100" align="center">
                        <template slot-scope="scope" class="align-center">
                            <el-button type="text" class="solt-button" size="small" @click="editItem"></el-button>
                            <el-button type="text" class="solt-button" size="small" @click="delItem(scope.row.index)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="parts-pagination">
                    <Pager :page="page" @chengeCurrentPage="chengeCurrentPage"></Pager>
                </div>
            </div>
            <!--OE-OEM-->
            <el-dialog :visible.sync="dialog.show.oeOem" class="oe-oem-dialog" :modal=false height="265" width="500">
                <span slot="title">OE-OEM</span>
                <el-table :data="dialog.list.oeOem" class="parts-oa-table" border height="180" width="500">
                    <el-table-column prop="brand" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="tempNum" label="" align="center" width="400"></el-table-column>
                </el-table>
            </el-dialog>
            <!---->
            <el-dialog :visible.sync="dialog.show.suitType" class="oe-oem-dialog" :modal=false height="265" width="500">
                <span slot="title"></span>
                <el-table :data="dialog.list.suitType" class="parts-oe-table" border height="180" width="500">
                    <el-table-column prop="brand" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="carTrain" label="" align="center" width="100"></el-table-column>
                    <el-table-column prop="carType" label="" align="center" width="300"></el-table-column>
                </el-table>
            </el-dialog>
        </div>
        <NewParts :showOrHide="editOrNew.showOrHide" v-on:toggle-status="toggleStatus"
                :editOrNew="editOrNew.status"></NewParts>
    </div>
</template>
<script>
    import '../../assets/less/ylc.less';
    import Headers from '@/components/Head';
    import Pager from "@/pages/partsMaintain/Pagination";
    import NewParts from "@/pages/partsMaintain/NewParts";
    export default{
        data(){
            return {
                editOrNew: {
                    showOrHide: false,
                    status: true
                },
                breadList: [""],
                form: {
                    brand: '',
                    number: '',
                    tempNum: '',
                    partName: '',
                    partsNikName: '',
                    suitType: '',
                },
                // 
                page: {
                    total: 200,
                    currentPage: 1,
                    totalPage: 12
                },
                // 
                table: {
                    list: [
                        {
                            brand: '',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '',
                            partsNikeName: '',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: false
                        }, {
                            brand: '',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '',
                            partsNikeName: '',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: true
                        }, {
                            brand: '',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '',
                            partsNikeName: '',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: true
                        }],
                },
                //
                dialog: {
                    show: {
                        oeOem: false,
                        suitType: false
                    },
                    list: {
                        oeOem: [
                            {
                                brand: '',
                                tempNum: ''
                            }, {
                                brand: '',
                                tempNum: ''
                            }, {
                                brand: '',
                                tempNum: ''
                            }, {
                                brand: '',
                                tempNum: ''
                            }],
                        suitType: [
                            {
                                brand: "",
                                carTrain: "",
                                carType: "2015"
                            }, {
                                brand: "",
                                carTrain: "",
                                carType: "2015"
                            }, {
                                brand: "",
                                carTrain: "",
                                carType: "2015"
                            }
                        ]
                    }
                },
            }
        },
        components: {Headers, Pager, NewParts},
        watch: {
            editOrNew: {
                handler (newV) {
                    console.log(newV);
                },
                deep: true
            }
        },
        methods: {
            showPartsDialog (el) {
                console.log(el)
                this.dialog.show.oeOem = true;
            },
            showPartsSuitType () {
                this.dialog.show.suitType = true;
            },
            chengeCurrentPage (page) {
                this.page = page;
            },
            toggleStatus (showOrHide) {
                this.editOrNew.showOrHide = showOrHide;
                this.breadList.splice(1, 1);
            },
            reset () {
                var _this = this;
                for (var i in this.form) {
                    _this.form[i] = "";
                }
            },
            newParts () {
                this.editOrNew.showOrHide = true;
                this.editOrNew.status = true;
                this.breadList.push("");
            },
            editItem () {
                this.editOrNew.showOrHide = true;
                this.editOrNew.status = false;
                this.breadList.push("");
            },
            delItem (index) {
                this.table.list.splice(index, 1);
            }
        }
    }
</script>



