                                </el-table>
                                <div class="table-footer">
                                    <div><span></span><p>279.00</p></div>
                                    <div><span></span><p>60.00</p></div>
                                    <div><span></span><p>219.00</p></div>
                                    <div class="add-link"><a class="a-tag-link" @click="pushRow"></a></div>
