                        <el-button type="primary"></el-button>
                    </div>
                </div>
            </div>
        </div>

        
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            class="edite-dialog"
            width="1000px"
            center>
            <div class="body-content">
                <el-popover
                    ref="popover"
                    popper-class="my-popper"
                    placement="bottom"
                    width="410"
                    v-model="searchPopover"
                    :visible-arrow="false"
                    trigger="click">
                    <div class="select-box">
                        <div class="search-input">
                            <el-input size="mini" v-model="searchQuery" suffix-icon="el-icon-search"></el-input>
                        </div>
                        <div class="search-list">
                            <ul>
                                <li v-for="(item,index) in conditions" :key="index"><a @click="hidePopover(item)">{{item.text}}</a></li>
