                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px">
                    <el-form-item label="">
                        <el-input v-model="ruleForm.project" placeholder="" size="small" suffix-icon="el-icon-arrow-down" v-popover:popover readonly></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="ruleForm.classify" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="ruleForm.time" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="ruleForm.singlePrice" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="ruleForm.price" size="small"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="ruleForm.remark" size="small"></el-input>
                    </el-form-item>
