                <el-button type="primary" @click="dialogVisible = false"> & </el-button>
                <el-button type="primary" @click="dialogVisible = false"> & </el-button>
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
        </el-dialog>
	</div>
</template>

<script>
import headers from "@/components/Head";
import CheckButtom from "@/pages/workHelper/CheckButtom";
import Pagination from "@/pages/workHelper/Pagination";
export default {
  name: "workhelper",
  data() {
    return {
        projectList:[{
            text:"01037",
            value:"1"
        },{
            text:"00149 ",
            value:"2"
        },{
            text:"00983 +650",
            value:"3"
        },{
            text:"01025 1.6",
            value:"4"
        },{
            text:"A00001 10000",
            value:"5"
        },{
            text:"00772 1554456",
            value:"6"
        },{
            text:"00772 1554456",
            value:"7"
        },{
            text:"00772 1554456 2",
            value:"8"
        },{
            text:"00772 15544563 2",
            value:"9"
        },{
            text:"00772 36",
            value:"10"
        }],
      hotList: [
        {
          id:"101",
          icon: "icon_xc",
          text: "",
          link:"/clean"
        },
        {
          id:"201",
          icon: "icon_ghjyjl",
          text: "",
          link:"/maintain"
        }
      ],
      activeName: "first",
      tableData:[{
          brand:"",
          hxcode:"BM 11427640862-A",
          ljcode:"11427640862",
          ljname:"",
          ljnickname:"",
          price4S:"240.00",
          price:"140.00",
          count:2,
          total:"140.00"
      },{
          brand:"",
          hxcode:"CASTPOL JHZX 0W40-209L",
          ljcode:"MO201543",
          ljname:" 0W-40 A3 209L",
          ljnickname:" 0W-40 A3 209L",
          price4S:"98.50",
          price:"78.50",
          count:1,
          total:"78.50"
      }],
      isPush:false,
      tableData2:[{
          gscode:"10012",
          gsname:"",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      }],
      page1:{
          total:196,
          currentPage:1,
          totalPage:6
      },
      page2:{
          total:200,
          currentPage:1,
          totalPage:12
      },
      dialogVisible:false,
      ruleForm: {
          project:"",
          classify:"",
          time:"",
          singlePrice:"",
          price:"",
          remark:""
      },
      rules: {

      },
      searchQuery:"",
      searchPopover:false
    };
  },
  components: { headers, CheckButtom,Pagination },
  computed:{
    conditions:function () {
        var items = this.projectList;
        var result=[];
        if (this.searchQuery !== '') {

            items.forEach((item) => {
                if(item.text.indexOf(this.searchQuery)!=-1){
                    result.push(item);
                }
            });
            return result;
        } else {
            return items;
        }
    }
  },
  methods: {
    handleClick:function(index, rows){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.isPush = false;
            rows.splice(index, 1);
            this.$message({
                type: 'success',
                message: '!'
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
        
    },
    chengeCurrentPage1:function(page){
        this.page1 = page;
        console.log(this.page1)
    },
    chengeCurrentPage2:function(page){
        this.page2 = page;
        console.log(this.page2)
    },
    showDialog:function(){
        this.dialogVisible = true;
    },
    hidePopover:function(item){
        this.ruleForm.project = item.text;
        this.searchPopover = false;
    },
    pushRow:function(){
        if(!this.isPush){
            this.isPush = true;
            this.tableData2.push({
                gscode:"10012",
                gsname:"",
                basenum:"",
                gssingleprice:"24.00",
                gsprice:"280.00"
            });
        }
    },
    saveClick:function(row){
        if(row.gsname!=''&&row.basenum!=''){
            this.isPush = false;
        }else{
            this.$message({
                message: '',
                type: 'warning'
            });
        }
    }
  }
};
</script>

<style lang="less">
.bread-crumb{
    height: 50px;
    background: #fff;
    margin-top: 10px;
    padding: 0 35px;
    span{
        display: inline-block;
        float: left;
        margin-right: 5px;
        line-height: 50px;
        height: 50px;
    }
    .crumb-item{
        padding-right:15px;
        font-size: 16px;
        color: #BFBDBD;
        font-weight: 700;
        background: url(./images/icon_arrow_right_cur.png) no-repeat center right;
    }
    .current-crumb{
        font-size: 16px;
        color: #5681C2;
    }
}
.edite-dialog{
    .el-dialog{
        border-radius: 4px;
    }
    .el-dialog--center .el-dialog__header{
        height: 24px;
        padding-top: 2px;
        border-bottom: 2px solid #C1D1E5;
        .el-dialog__title{
            float: left;
            font-size: 14px;
            color: #2E2E2E;
            font-weight: bold;
        }
        .el-dialog__headerbtn{
            top: 2px;
            font-size: 22px;
            [class*=" el-icon-"]{
                font-weight: bold;
                color: #BFBDBD;
            }
        }
    }
    .el-dialog__footer{
        padding-bottom: 55px;
    }
    .body-content{
        width: 550px;
        margin: 0 auto;
        .el-select .el-input{
            width: 430px;
        }
        .el-input--small .el-input__inner{
            height: 34px;
            border-color: #C1D1E5;
        }
        .el-form-item__label{
            padding-right: 30px;
            font-size: 14px;
            color: #2E2E2E;
        }
    }
    .el-button--primary{
        padding: 7px 10px;
        margin:0 25px;
        font-size: 14px;
        color: #FFFFFF;
        border-radius: 4px;
    }
    
    
}
.my-popper{
    margin-top: 2px!important;
    padding: 5px 10px;
    .select-box{
        max-height: 300px;
        .search-input{
            padding-bottom: 5px;
            .el-input--mini .el-input__inner {
                border-color: #DBE4ED;
            }
        }
        .search-list{
            height: 250px;
            overflow: auto;
            margin:0 -10px;
            a{
                display: block;
                height: 28px;
                line-height: 28px;
                font-size: 12px;
                color: #6E6E6E;
                padding-left:20px;
            }
            a:hover{
                background: #EBF1FA;
            }
        }
    }
    
}

.page-container {
  height: auto;
  overflow: hidden;
  margin: 0 auto;
  padding: 10px 20px;
  .fr {
    float: right;
  }
  
  .work-content {
    height: auto;
    overflow: hidden;
    border-radius: 2px;
    padding: 10px 15px 15px;
    background: #fff;
    margin-bottom: 10px;
    .work-header {
      height: 30px;
      padding: 5px 0;
      .header-title {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        color: #999;
        font-weight: bold;
        font-size: 14px;
        padding-left: 10px;
        background: url(./images/icon_arrow-right.png) no-repeat left center;
      }
      .header-tool {
        display: inline-block;
        .el-button--mini{
            padding: 7px 10px;
            border: 1px solid #5681C2;
            border-radius: 4px;
            font-size: 14px;
        }
        .el-button--mini a{
            color: #2e2e2e;
        }
      }
    }
    .work-header-line {
      border-bottom: 2px solid #dbe4ed;
    }
    .work-wraper {
      height: auto;
      overflow: hidden;
      .grid-content {
        height: auto;
        overflow: hidden;
        border-top: 1px solid #dbe4ed;
        border-left: 1px solid #dbe4ed;
        border-radius: 4px;
        .grid-item {
          height: 40px;
          line-height: 40px;
          padding: 0 15px;
          border-bottom: 1px solid #dbe4ed;
          border-right: 1px solid #dbe4ed;
          .item-title {
            color: #2e2e2e;
            font-size: 14px;
            font-weight: 500;
            float: left;
            padding-right: 30px;
          }
          p {
            color: #6e6e6e;
            font-size: 12px;
          }
        }
      }
      .checkbox-button-box {
        height: auto;
        overflow: hidden;
      }
      .work-table {
        position: relative;
        padding-top: 15px;
        height: auto;
        overflow: hidden;
        .ab-top-box{
            position: absolute;
            top: 10px;
            right: 0;
            z-index: 99;
            .el-button--primary{
                padding: 5px 10px;
                font-size: 14px;
                color: #FFFFFF;
            }
        }
        .el-table th{
            border-bottom: 2px solid #e6ebf5;
        }
        .el-table .cell{
            font-size: 12px;
            color: #6E6E6E;
        }
        .el-table th>.cell{
            font-size: 14px;
            color: #2E2E2E;
        }
        .el-button--text{
           color: #5681C2;
           text-decoration: underline;
        }
        .el-table td{
            padding: 5px 0;
        }
        .el-input-number--mini{
            width: 120px;
            height: 32px;
        }
        .el-input-number__decrease,
        .el-input-number__increase{
            height: 28px;
            top: 1px;
            border-radius: 0;
            background: #fff;
        }
        .el-input--mini .el-input__inner{
            height: 30px;
            border-radius: 0;
        }
        .my-tabs .el-tabs__header{
            margin-bottom: 1px;
            border:none;
        }
        .my-tabs .el-tabs__header .el-tabs__nav{
            border:none;
        }
        .my-tabs .el-tabs__item{
            padding: 0 10px;
            height: 30px;
            line-height: 30px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item{
            border: 1px solid #C1CCD8;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #6E6E6E;
            margin-right:2px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item.is-active{
            border: 1px solid #5681C2;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #5681C2;
        }
        .table-footer{
            border: 1px solid #DBE4ED;
            height: 20px;
            padding: 10px 20px;
            border-radius: 0 0 4px 4px;
            div{
                display: inline-block;
                float: left;
                line-height: 20px;
                margin-right: 100px;
                span{
                    display: inline-block;
                    float: left;
                    height: 20px;
                    font-size: 14px;
                    color: #2E2E2E;
                    font-weight: bold;
                }
                p{
                    margin-left: 60px;
                    font-size: 12px;
                    color: #C91523;
                }
            }
            .add-link{
                float: right;
                margin-right: 50px;
                .a-tag-link{
                    font-size: 12px;
                    color: #5681C2;
                    cursor: pointer;
                }
            }
        }
        .table-bottom-wraper{
            padding: 16px 0;
            height: 20px;
        }
      }
      .input-number-table{
            .el-input-number__decrease,
            .el-input-number__increase{
                top:1px!important;
            }
      }
      .work-table-footer{
          padding-top:40px;
          text-align: center;
          .el-button--primary{
              padding: 7px 10px;
          }
      }
    }
  }
}


// @media screen and (max-width: 1366px) {
