import Vue from 'vue'
import Router from 'vue-router'
import PartsMaintain from '@/pages/partsMaintain/PartsMaintain'
import WorkHelper from '@/pages/workHelper/WorkHelper'
import PartErrorCorrection from '@/pages/partErrorCorrection/partErrorCorrection'
import PartErrCorrHistory from '@/pages/partErrorCorrectionHistory/partErrorCorrectionHistory'
import Maintain from "@/pages/maintain/Maintain"
import selectMountings from '@/pages/selectMountings/selectMountings'
import Clean from '@/pages/clean/Clean'
import Tire from '@/pages/tire/Tire';
import Deep from '@/pages/deep/deep';
import Battery from '@/pages/workHelper/battery'
import Repair from '@/pages/workHelper/repair'
Vue.use(Router)


export default new Router({
    routes: [{
        path: '/', // 
        name: '',
        component: PartErrCorrHistory
    }, {
        path: '/partErrorCorrection', // 
        name: '',
        component: PartErrorCorrection
    }, {
        path: '/workhelper', // 
        name: '',
        component: WorkHelper
    }, {
        path: '/maintain', // 
        name: '',
        component: Maintain
    }, {
        path: '/clean', // 
        name: '',
        component: Clean
    },{
        path: '/partsMaintain', // 
        name: '',
        component: PartsMaintain
    }, {
        path: '/selectMountings', // 
        name: '',
        component: selectMountings
    }, {
        path: '/tire', // 
        name: '',
        component: Tire
    }, {
        path: '/deep', // 
        name: '',
        component: Deep
    },{
    	path: '/battery', // 
        name: '',
        component: Battery
    },{
    	path: '/repair', // 
        name: '',
        component: Repair
    }]
})


