<template>
    <div class="new-parts-maintain" v-show="showOrHide">
        <div class="base-info block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name"></span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" :rules="rules" label-width="100px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="" prop="partsType">
                            <template v-if="editOrNew">
                                <el-select :key="1" v-model="form.basicInfo.partsType" placeholder="">
                                    <el-option :label="item.name" :key="item.value" v-for="item in form.partsTypeTree"
                                            :value="item.value"></el-option>
                                </el-select>
                            </template>
                            <template v-else="!editOrNew"><span :key="2">{{ form.basicInfo.partsType || "" }}</span></template>
                        </el-form-item>
                        <el-form-item label="" prop="partsBrand">
                            <el-select v-model="form.partsBrand" placeholder="">
                                <el-option :label="item.name"
                                        v-for="item in form.partsBrand"
                                        :key="item.value"
                                        :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="" prop="number">
                            <template v-if="editOrNew">
                                <el-input v-model="form.basicInfo.number" placeholder=""></el-input>
                            </template>
                            <template v-else="!editOrNew">{{ form.basicInfo.number || "232335454645" }}</template>

                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.tempNo" placeholder="OE OEM"></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="" prop="quality">
                            <el-select v-model="form.basicInfo.quality" placeholder="">
                                <el-option :label="item.name"
                                        :key="item.value"
                                        v-for="item in form.quality"
                                        :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.partsPic" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="" prop="partsName">
                            <el-input v-model="form.basicInfo.partsName" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.partsNikName" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.specValue" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.unitName" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="">
                            <el-input v-model="form.basicInfo.note" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select v-model="form.basicInfo.status" placeholder="">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="price block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name"></span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" :rules="rules" label-width="100px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="" prop="purchasePrice">
                            <el-input v-model="form.price.purchasePrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.price.profitRate" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="4S">
                            <el-input v-model="form.price._4sPrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="baseSalePrice">
                            <el-input v-model="form.price.baseSalePrice" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="">
                            <el-input v-model="form.price.minSalePrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.price.turnPrice" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="suit-type block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name"></span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" label-width="85px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="">
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class="item-row" v-for="item in form.suit.suitType">
                        <el-form-item class="three-selected" :label="item.label">
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </el-form>
                <div class="underline-title finger suit" @click="newSuitType"></div>
            </div>
        </div>
        <div class="oe-oem block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name">OE/OEM</span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form.oeOem" label-width="100px" class="parts-form-inline-half">
                    <div class="item-row-half">
                        <div class="header-title"></div>
                        <el-form-item class="staticOe" label="OE"></el-form-item>
                    </div>
                    <div class="item-row-half">
                        <div class="header-title"></div>
                        <el-form-item class="staticOe" label=""></el-form-item>
                    </div>
                </el-form>
                <el-form class="parts-form-inline-half" :key="item.value" v-for="item in form.oeOem.list">
                    <div class="item-row" >
                        <el-form-item label="OEM">
                            <el-select v-model="item.brand" placeholder="">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" style="margin-left: 31px;">
                            <el-input v-model="item.number" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-form>
                <div class="underline-title finger suit oe" @click="newTempNum"></div>
            </div>
        </div>
        <div class="button-list">
            <el-button type="primary" @click="save"></el-button>
            <el-button type="primary" class="reset" @click="goBack"></el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "new-parts-maintain",
        props: {
            showOrHide: {
                type: Boolean,
                default () {
                    return true
                }
            },
            editOrNew: {
                type: Boolean,
                default () {
                    return true;
                }
            }

        },
        data () {
            return {
                form: {
                    basicInfo: {
                        partsType: "",
                        partsBrand: "",
                        number: "",
                        tempNo: "",
                        quality: "",
                        partsPic: "",
                        partsName: "",
                        partsNikName: "",
                        specValue: "",
                        unitName: "",
                        note: "",
                        status: ""
                    },
                    price: {
                        purchasePrice: "",
                        profitRate: "",
                        _4sPrice: "",
                        baseSalePrice: "",
                        minSalePrice: "",
                        turnPrice: ""
                    },
                    suit: {
                        isCommon: "",
                        suitType: [
                            {
                                label: "1"
                            }
                        ]
                    },
                    oeOem: {
                        brand: [
                            {
                                name: "",
                                value: "00222"
                            },
                            {
                                name: "",
                                value: "00223"
                            },
                        ],
                        number: "",
                        list: [
                            {
                                brand: "",
                                number: "",
                            }
                        ]
                    },
                    partsTypeTree: [
                        {
                            name: "1",
                            value: "0001",
                            children: [{
                                name: "",
                                value: "0002"
                            }]
                        }, {
                            name: "",
                            value: "0003",
                            children: [{
                                name: "1",
                                value: "0004"
                            }]
                        }
                    ],
                    partsBrand: [
                        {
                            name: "1",
                            value: "00011"
                        }, {
                            name: "2",
                            value: "00022"
                        },
                    ],
                    quality: [
                        {
                            name: "",
                            value: "yuanchang"
                        }, {
                            name: "",
                            value: "brand"
                        }, {
                            name: "",
                            value: "gaibao"
                        }, {
                            name: "",
                            value: "chaiche"
                        }
                    ],
                    defaultProps: {
                        children: 'children',
                        label: 'name'
                    }
                },
                rules: {
                    partsType: [{ required: true, message: '', trigger: 'change' }],
                    partsBrand: [{ required: true, message: '', trigger: 'change' }],
                    number: [{ required: true, message: '', trigger: 'blur' }],
                    quality: [{ required: true, message: '', trigger: 'blur' }],
                    partsName: [{ required: true, message: '', trigger: 'blur' }],
                    purchasePrice: [{ required: true, message: '', trigger: 'blur' }],
                    baseSalePrice: [{ required: true, message: '', trigger: 'blur' }],
                }
            }
        },
        methods: {
            newTempNum () {
              var len = this.form.oeOem.list.length + 1;
              this.form.oeOem.list.push({
                  brand: "",
                  number: ""
              })
            },
            newSuitType () {
                var len = this.form.suit.suitType.length;
                this.form.suit.suitType.push({
                    label: "" + (+len + 1)
                });
            },
            save () {
//                this.$refs[this.form.basicInfo].validate((valid) => {
//                    if (valid) {
//                        alert('submit!');
//                    } else {
//                        console.log('error submit!!');
//                        return false;
//                    }
//                });
                this.showOrHide = false;
                this.$emit("toggle-status", this.showOrHide)
            },
            goBack () {
                this.showOrHide = false;
                this.$emit("toggle-status", this.showOrHide)
            }
        },
        created () {
            console.log(this.editOrHide)
        }
    }
</script>


